<template>
  <astro-section header="ガイド紹介">
    <div v-for="guide in guides">
      <div class="panel panel-default">
        <div class="panel-body">
          <h4>{{guide.name}}</h4>
        </div>
        <img :src="require(`assets/images/guides/${guide.fileName}`)" />
        <div class="panel-footer">
          <p v-for="line in guide.description.split('\n')">{{line}}</p>
        </div>
      </div>
    </div>
  </astro-section>
</template>

<script>
  import astroSection from '../organisms/Section.vue'

  export default {
    name: 'Guides',
    components: { astroSection },
    computed: {
      guides () {
        return this.$store.state.guides
      },
    },
  }
</script>

<style scoped>
  .panel {
    color: black;
  }
  .panel img {
    margin-bottom: 30px
  }
</style>



