<template>
  <div>
    <astro-section header="お知らせ">
      <div class="notice" v-for="notice in notices">
        <h4>{{notice.title}}</h4>
        <p v-for="line in notice.context.split('\n')">{{line}}</p>
      </div>
    </astro-section>
  </div>
</template>

<script>
  import astroSection from '../organisms/Section.vue'

  export default {
    name: 'Notices',
    components: { astroSection },
    computed: {
      notices () {
        return this.$store.state.notices
      },
    },
  }
</script>

<style>
  .notice {
    margin-top: 50px;
  }
</style>



