<template>
  <astro-section header="料金">
    <table class="table table-bordered">
      <thead>
        <tr>
          <th v-for="text in header" class="text-center">
            {{text}}
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="row in rows" class="text-center">
          <td v-for="text in row">{{text}}</td>
        </tr>
      </tbody>
    </table>
  </astro-section>
</template>

<script>
  import head from 'lodash/head'
  import drop from 'lodash/drop'
  import astroSection from '../organisms/Section.vue'

  export default {
    name: 'Price',
    components: { astroSection },
    computed: {
      header () {
        return head(this.$store.state.price)
      },
      rows () {
        return drop(this.$store.state.price)
      },
    },
  }
</script>

<style>
</style>



