import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import fp from 'lodash/fp'
import drop from 'lodash/drop'

import Page from './pages/Page'

import 'assets/less/main.less'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    notices: null,
    price: null,
    guides: null,
  },
  mutations: {
    setNotices (state, notices) {
      state.notices = notices
    },
    setPrice (state, price) {
      state.price = price
    },
    setGuides (state, guides) {
      state.guides = guides
    },
  },
})

const NOTICES_SHEET_ID = '1-wO5Gt9w46SI4B2zAZXN7eIL2EfLXdy6T5F0_izOgQQ'
const PRICE_SHEET_ID = '16Ekkrgaohoncn7_vtCctTE12GPJbk4APpsd74jir4fQ'
const GUIDES_SHEET_ID = '14J24CrZjD7HNFhgwwoSthI0VxDCciMNc9Og9Luhp8Bo'

const genSheetUrl = id => `https://spreadsheets.google.com/feeds/cells/${id}/od6/public/values?alt=json`

const getSheetCells = id => axios.get(genSheetUrl(id))
  .then(result => result.data.feed.entry.map(e => e.gs$cell))

getSheetCells(NOTICES_SHEET_ID)
  .then(fp.groupBy(cell => cell.row))
  .then(fp.map(row => ({ title: row[0].$t, context: row[1].$t })))
  .then(drop)
  .then(notices => store.commit('setNotices', notices))

getSheetCells(PRICE_SHEET_ID)
  .then(fp.groupBy(cell => cell.row))
  .then(fp.map(row => row.map(cell => cell.$t)))
  .then(table => store.commit('setPrice', table))

getSheetCells(GUIDES_SHEET_ID)
  .then(fp.groupBy(cell => cell.row))
  .then(fp.map(row => ({ fileName: row[0].$t, name: row[1].$t, description: row[2].$t })))
  .then(drop)
  .then(guides => store.commit('setGuides', guides))

new Vue({
  template: '<page />',
  components: { Page },
  store,
}).$mount('#app')



