<template>
  <div>
    <parallax :fixed="true">
      <img :src="kumanokodo" alt="" />
    </parallax>
    <div class="smoke"></div>
    <astro-jumbotron/>
    <div class="content">
      <astro-notices />
      <astro-price />
      <astro-guides />
    </div>
  </div>
</template>

<script>
  import Parallax from 'vue-parallaxy'
  import astroJumbotron from '../templates/Jumbotron'
  import astroNotices from '../templates/Notices'
  import astroPrice from '../templates/Price'
  import astroGuides from '../templates/Guides'

  import hoshizora from 'assets/images/background/hoshizora.jpg'
  import kumanokodo from 'assets/images/background/kumanokodo.jpg'

  export default {
    name: 'Page',
    components: { astroJumbotron, astroNotices, astroPrice, astroGuides, Parallax },
    data: () => ({ hoshizora, kumanokodo }),
  }
</script>

<style scoped>
  .smoke {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: black;
    opacity: 0.5;
  }
  .content {
    text-align: center;
  }
</style>



